import { useState } from 'react'
import '../css/Menu.css'

function Menu(props) {
	let [active, setActive] = useState(0)
	let items = props.items ?? []
	return (
		<div>
			{items.map(
				(item, index) => (
					<MenuItem key={index} onClick={() => setActive(index)} active={active===index} icon={item.icon}>
						{item.title}
					</MenuItem>
				)
			)}
		</div>
	)
}

function MenuItem(props) {
	let active = props.active
	return (
		<div className={`relative py-2 px-7 my-1 text-gray-50 text-lg font-bold ${active?'itm-active':'itm-effect'}`} onClick={props.onClick}>
			<a href="#home" className={``}><i className={`fa fa-${props.icon}`}></i> {props.children}</a>
		</div>
	)
}

export {
	Menu,
	MenuItem
}