import { useState } from 'react'
import '../css/SideMenu.css'
import { Menu } from './Menu'


function SideBar(props) {
	let visible = props.visible
	let visibleClass = visible ? "left-0" : "-left-64"
	let width = visible ? "w-64" : "w-0"
	return (
		<div className={`${width} p-0 transition-all duration-300 ease-in-out`}>
			<div className={`fixed bg-blue-900 ${width} h-full ${visibleClass} transition-all duration-300 ease-in-out`}>
				{props.children}
			</div>
		</div>
	)
}

function TopBar(props) {
	return (
		<nav className="sticky top-0 w-screen text-gray-50 bg-blue-900 flex justify-between">
			<button className="p-3 text-2xl font-semibold"><i className="fa fa-rocket"></i> Edwin</button>
			<button className="p-3 mr-2 hover:text-yellow-600 transition-colors duration-300 ease-in-out" onClick={props.onClick}>
				<span className="text-2xl"><i className="fa fa-bars"></i></span>
			</button>
		</nav>
	)
}

function Navigator(props) {

	let menu = [
		{
			title: 'Home',
			icon: 'home'
		},
		{
			title: 'Portafolio',
			icon: 'th-list'
		},
		{
			title: 'Contact',
			icon: 'id-badge'
		},
		{
			title: 'Option 1'
		},
		{
			title: 'Option 2'
		},
		{
			title: 'Option 3'
		}
	]

	let [toggle, setToggle] = useState(true)

	return (
		<div className={`${props.className}`}>
			<div className='md:flex md:flex-wrap dark:bg-gray-700'>
				<TopBar onClick={() => setToggle(!toggle)}/>
				<SideBar visible={toggle}>
					<div className="h-12"></div>
					<Menu items={menu} />
				</SideBar>
				<div className="h-screen">
					{props.children}
				</div>
			</div>
		</div>
	)
}

export {
	Navigator
}